import { h } from "preact";
import { useState } from "preact/hooks";
import style from "./style";
import styled from "styled-components";
import ScanLottie from "../../components/scanlottie";
import SwitchSelector from "react-switch-selector";
import Alert from "../../components/alert";
import NfcConsole from "../../components/nfc-console";

const ScanButton = styled.button`
  background: #ffc86b;
  color: black;
  border: none;
  padding: 12px 40px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 20px;
`;

const Home = () => {
  const [feed, setFeed] = useState("");
  const [feed2, setFeed2] = useState("");
  const [feed3, setFeed3] = useState("");
  const [feed4, setFeed4] = useState("");
  const [buttonText, setButtonText] = useState("Start Scan");

  const options = [
    {
      label: "Read",
      value: "read",
      selectedBackgroundColor: "#581166"
    },
    {
      label: "Write",
      value: "write",
      selectedBackgroundColor: "#581166"
    }
  ];

  const onChange = newValue => {
    console.log(newValue);
  };

  const initialSelectedIndex = options.findIndex(
    ({ value }) => value === "read"
  );

  const nfcScan = async () => {
    try {
      const reader = new NDEFReader();
      await reader.scan();
      setButtonText("Scanning...");

      reader.addEventListener("error", () => {
        setFeed(feed => [
          ...feed,
          "Argh! Cannot read data from the NFC tag. Try a different one?"
        ]);
      });

      reader.addEventListener("reading", ({ message, serialNumber }) => {
        setFeed(`${serialNumber}`);
        
        for (const record of message.records) {
          switch (record.recordType) {
              case "text":
                  const textDecoder = new TextDecoder(record.encoding);
                  setFeed(feed2 => [`> Message: `, textDecoder.decode(record.data)]);
                  
                  break;
              case "url":
                const textDecoder2 = new TextDecoder(record.encoding);
                setFeed(feed3 => [`> Message: `, textDecoder2.decode(record.data)]);
                  break;
              default:
                  // TODO: Handle other records with record data.
          }
      }
        //  if (message.records.length > 0) {
        //    setFeed(feed => [...feed, `> Message: ${message.records[0]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[1]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[2]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[3]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[4]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[5]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[6]}`]);
        //    setFeed(feed => [...feed, `> Message: ${message.records[7]}`]);
        //  }
      });
    } catch (error) {
      setFeed(feed => [...feed, `Argh! ${error}`]);
    }
  };

  const scanButtonHandler = () => {
    nfcScan();
  };

  return (
    <div class={style.home}>
      <Alert />
      <div class={style.toggle}>
        <SwitchSelector
          inputName="NFC Mode"
          onChange={onChange}
          options={options}
          initialSelectedIndex={initialSelectedIndex}
          backgroundColor={"#f7f7f5"}
          fontColor={"#444"}
        />
      </div>
      <ScanLottie />
      <ScanButton onClick={scanButtonHandler}>{buttonText}</ScanButton>
      <NfcConsole feed={feed} feed2={feed2} feed3={feed3}/>
    </div>
  );
};

export default Home;
